.header {
    position: relative;
    height: 95vh;
    background: url(../img/header-background.jpg) center / cover no-repeat;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 80%);
    &__logo {
        position: absolute;
        left: 5rem;
        top: 5rem;
        height: 7rem;
        width: 5.6rem; }
    &__box {
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }
